<template>
    <div class="star" @click.stop="star">
        <div v-for="(starType, index) in itemClasses" :key="index" class="starItem" :class="starType" @click="star(index,$event)"></div>
    </div>
</template>

<script>
const LENGTH = 5
const ON = 'on'
const OFF = 'off'
const HALF = 'half'
export default {
  name: 'star',
  props: {
    score: {
      type: Number
    }
  },
  computed: {
    itemClasses () {
      let result = []
      let score = Math.floor(this.score * 2) / 2
      let hasDecimal = score % 1 !== 0
      let intNum = Math.floor(score)
      for (let i = 0; i < intNum; i++) {
        result.push(ON)
      }
      if (hasDecimal) {
        result.push(HALF)
      }
      while (result.length < LENGTH) {
        result.push(OFF)
      }
      return result
    }
  },
  methods: {
    star: (index, event) => {
      this.score = index + 1
      console.log(this.score)
    }
  }
}
</script>

<style scoped>
  * {
      margin: 0;
      padding: 0;
  }

  .starItem {
      height: 0.6rem;
      width: 0.6rem;
      margin-right: 0.2rem;
      background-size: 100%;
      background-repeat: no-repeat;
      display: inline-block;
  }

  .starItem.on {
      background-image: url('../assets/img/starOn.png')
  }

  .starItem.off {
      background-image: url('../assets/img/starOff.png')
  }

  .starItem.half {
      background-image: url('../assets/img/starHalf.png')
  }
</style>



