<template>
  <div class="prictice">
    <div class="fixedArea">
      <div class="header">
        <div class="searchBar">
          <input type="text" placeholder="请输入您想搜索的名师/拳馆" @focus="showHistory" v-model="search">
            <img src="../assets/img/searchIcon.png" @click="iptSearch">
        </div>
        <div class="position" @click="getPosition">
          <img src="../assets/img/position.png">
        </div>
      </div>
      <div class="sortBar">
        <span @click="handleSort">
          <span id="sortMode">综合排序</span>
          <img class="pulldown" src="../assets/img/pulldown.png">
        </span>
        <span style="color: #fff;">时间选择</span>
        <ul class="sortMenu" v-show="isShow">
          <li v-for="(sort, index) in sorts" :key="index" class="sortLink" :class="{ actived: sort.isActive }" @click="changeSort(index)">{{
            sort.link }}</li>
        </ul>
      </div>
    </div>
    <div class="container" v-if="rows.length > 0">
      <mt-loadmore :top-method="loadTopItem" :bottom-method="loadBottomItem" :bottom-all-loaded="itemAll" ref="loadmoreItem" :auto-fill="false">
        <div class="rows" v-for="(row, index) in rows" :key="index">
          <div class="row" @click="showDetails(index)">
            <div class="rowLeft">
              <span class="headPic">
                <img :src="row.fPhoto">
                <div class="usertype">{{ row.fUserType === 'T' ? '教' : (row.fUserType === 'C' ? '馆' : '学') }}</div>
              </span>
            </div>
            <div class="rowRight">
              <div class="userName">{{ row.fName }}</div>
              <div class="information">学员数&ensp;{{ row.student }}&emsp;|&emsp;粉丝数&ensp;{{ row.fans }}</div>
              <div class="rateAndDistance">
                <star :score="row.fAvgStar / 2"></star>
                <span class="distance">{{ row.fAddress }}</span>
              </div>
            </div>
          </div>
          <div class="tips">
            <span class="tip" v-for="(tip, index) in row.tips" :key="index">{{ tip }}</span>
          </div>
        </div>
      </mt-loadmore>
    </div>
    <div class="mask" v-show="hasMask" @click="handleSort"></div>
    <div class="searchHistory" v-if="!isFocus" @click="isFocus = !isFocus">
      <div class="historyHead">
        <span>搜索历史</span>
        <span class="delete" @click="clearHis">
          <img src="../assets/img/delete.png">
        </span>
      </div>
      <ul class="historyList" v-if="historyxs">
        <li v-for="(item, index) in searchList" :key="index">{{ item }}</li>
        <div class="clearFix"></div>
      </ul>
    </div>
    <baidu-map :center="center" :zoom="zoom" @ready="handler"></baidu-map>
    <BottomNav></BottomNav>
  </div>
</template>

<script>
import axios from 'axios'
import { getTeachers } from '../api/getdata'
import star from '../components/Star'
import BmGeolocation from 'vue-baidu-map/components/controls/Geolocation.vue'
import storage from 'good-storage'
import { saveSearch } from '../../static/js/cache.js'
import { MessageBox, Loadmore } from 'mint-ui'
import BottomNav from '../components/BottomNav'
export default {
  name: 'teachers',
  data () {
    return {
      sortType: 'synthesis',
      page: 1,
      itemAll: false,
      center: {lng: 0, lat: 0},
      zoom: 15,
      search: '',
      searchList: [],
      historyxs: false,
      isFocus: true,
      hasMask: false,
      isShow: false,
      sorts: [{
        link: '综合排序',
        isActive: true,
        status: 'synthesis'
      }, {
        link: '距离最近',
        isActive: false,
        status: 'distance'
      }, {
        link: '教练评分最高',
        isActive: false,
        status: 'coachScore'
      }, {
        link: '拳馆评分最高',
        isActive: false,
        status: 'martialScore'
      }],
      rows: []
    }
  },
  mounted () {
    // this.getPosition()
    // this.getDataList()
  },
  methods: {
    getDataList () {
      let data = getTeachers({longitude: this.center.lng, latitude: this.center.lat, page: this.page, status: this.sortType})
      data.then(res => {
        console.log(res.data)
        this.rows = this.rows.concat(res.data)
      })
    },
    showDetails (index) {
      if (this.rows[index].fUserType === 'T') {
        localStorage.setItem('fInstructorId', this.rows[index].fInstructorId)
        localStorage.setItem('fUserId', this.rows[index].fUserID)
        this.$router.push('/home/teachers/userInfor')
      } else {
        localStorage.setItem('fClubId', this.rows[index].fClubId)
        localStorage.setItem('fUserId', this.rows[index].fUserID)
        this.$router.push('/home/teachers/gymInfo')
      }
    },
    handleSort () {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    },
    changeSort (index) {
      this.sortType = this.sorts[index].status
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
      document.getElementById('sortMode').innerHTML = this.sorts[index].link
      for (var i = 0; i < this.sorts.length; i++) {
        this.sorts[i].isActive = false
        this.sorts[index].isActive = true
      }
      let data = getTeachers({longitude: this.center.lng, latitude: this.center.lat, page: this.page, status: this.sortType})
      data.then(res => {
        console.log(res.data)
        // this.showAddress = false
        this.rows = res.data
      })
    },
    showHistory () {
      this.isFocus = false
      this.$emit('initSearchPage')
      this.searchList = []
      let searches = storage.get('_search_')
      this.searchList = searches || []
      if (this.searchList.length > 0) {
        this.historyxs = true
      } else {
        this.historyxs = false
      }
    },
    iptSearch () {
      this.isFocus = true
      if (this.search !== '') {
        saveSearch(this.search)
        let params = {
          majorInfo: this.search
        }
        this.$emit('handleSearch', params)
        this.isFocus = true
        this.search = ''
        this.$router.push('/home/teachers/search')
      } else {
        MessageBox({
          title: '提示',
          message: '搜索内容不能为空',
          showCancleButton: true
        })
      }
    },
    historysearch (item) {
      saveSearch(item)
      this.search = item
      this.iptSearch()
      this.historyxs = false
    },
    clearHis () {
      storage.remove('_search_')
      this.searchList = []
      this.historyxs = false
    },
    getPosition () {
      console.log('获取我的位置')
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(showPosition, showError)
      } else {
        alert('不支持定位')
      }
      function showPosition (position) {
        var myak = 'a7XU2GjlrySMNkj5lyqXbEhVNOBv82z0'
        var lat = position.coords.latitude
        var lng = position.coords.longitude
        var myposition = lat + ',' + lng
        console.log('纬度:' + lat + ',经度:' + lng)
        axios.get(`http://api.map.baidu.com/geoconv/v2/?mcode=sha1:C4:83:13:EA:95:BF:73:F0:8F:78:ED:57:B6:3F:46:0A:58:4A:02:17&location=${myposition}&output=json&posi=1&ak=${myak}`)
          .then(res => {
            console.log(res)
          })
      }
      function showError () {
        alert('出错了')
      }
    },
    handler ({BMap, map}) {
      let _this = this
      var geolocation = new BMap.Geolocation()
      geolocation.getCurrentPosition(r => {
        // console.log(r.address)
        var address = r.address.city + r.address.district + r.address.street + r.address.street_number
        console.log(address)
        // this.myInfo.addRess = address
        _this.center = {lng: r.longitude, lat: r.latitude}
        this.center = _this.center
        console.log(this.center)
        let data = getTeachers({longitude: this.center.lng, latitude: this.center.lat, page: this.page, status: this.sortType})
        data.then(res => {
          console.log(res.data)
          this.showAddress = true
          this.rows = res.data
        })
      })
    },
    loadTopItem () {
      this.rows = []
      this.page = 1
      this.getDataList()
      setTimeout(() => {
        this.$refs.loadmoreItem.onTopLoaded()
      }, 2000)
    },
    loadBottomItem () {
      this.page++
      this.getDataList()
      setTimeout(() => {
        this.$refs.loadmoreItem.onBottomLoaded()
      }, 2000)
    },
    isHaveMore (haveMore) {
      this.haveMore = true
      if (haveMore) {
        this.itemAll = false
      }
    }
  },
  components: {
    BottomNav,
    star,
    BmGeolocation,
    'mt-loadmore': Loadmore
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.4rem;
  }

  .header {
    padding: 0.16rem 0;
    height: 0.96rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
  }

  .position {
    margin-top: 0.12rem;
    margin-right: 0.4rem;
  }

  .position img {
    width: 0.48rem;
    height: 0.586667rem;
  }

  .searchBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 8.48rem;
    height: 1rem;
    border: 1px solid #e6e6e6;
    border-radius: 0.5rem;
    margin-left: 0.373333rem;
  }

  .searchBar input {
    outline: none;
    width: 6rem;
    margin-left: 0.4rem;
    border: none;
    outline: none;
  }

  .searchBar img {
    width: 0.48rem;
    height: 0.48rem;
    margin-right: 0.2rem;
  }

  .sortBar {
    display: flex;
    justify-content: space-between;
    background: #ffd200;
    height: 1.04rem;
    align-items: center;
    padding: 0 0.4rem;
    position: relative;
    z-index: 3;
  }

  .sortBar span:first-child {
    display: flex;
    align-items: center;
  }

  .sortBar span:last-child {
    color: #fff;
  }

  .pulldown {
    display: flex;
    align-items: center;
    width: 0.24rem;
    height: 0.16rem;
    margin-left: 0.08rem;
  }

  .pulldown img {
    width: 100%;
    height: 100%;
  }

  .container {
    padding: 0 0.4rem;
    margin: 2.32rem 0 2rem 0;
  }

  .row {
    display: flex;
    padding-top: 0.373333rem;
  }

  .headPic {
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 50%;
    display: inline-block;
    position: relative;
  }

  .headPic img {
    width: 100%;
    height: 100%;
    border-radius: 0.8rem;
  }

  .information {
    color: #666666;
    margin-top: 0.1rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 7.5rem;
    font-size: 0.36667rem;
  }

  .rowRight {
    width: 100%;
    margin-left: 0.373333rem;
    margin-top: 0.4rem;
    text-align: start;
  }

  .rateAndDistance {
    width: 100%;
    display: flex;
    justify-content: space-between;
    color: #ff8309;
    margin-top: 0.373333rem;
  }

  .distance {
    display: flex;
    align-items: center;
    margin-right: 0.5rem;
  }

  .tips {
    display: flex;
    padding: 0.24rem 0;
    border-bottom: 1px solid #e6e6e6;
  }

  .rows:last-child .tips:last-child {
    border: none;
  }

  .tip {
    height: 0.66666rem;
    border: 1px solid #666666;
    display: flex;
    align-items: center;
    border-radius: 0.33333rem;
    padding: 0 0.2rem;
    margin-left: 0.293333rem;
    color: #666666;
  }

  .tip:first-child {
    margin-left: 0;
  }

  .sortMenu {
    width: calc(100% - 0.8rem);
    padding: 0 0.4rem;
    list-style: none;
    background: #fff;
    position: absolute;
    top: 1.04rem;
    left: 0;
    z-index: 999;
  }

  .sortLink {
    text-align: start;
    padding: 0.2rem 0;
    color: #666666;
  }

  .sortLink:nth-child(2) {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }

  .sortLink:nth-child(3) {
    border-bottom: 1px solid #e6e6e6;
  }

  .mask {
    z-index: 2;
    position: absolute;
    top: -10rem;
    width: 100%;
    height: 2000%;
    background: #000000;
    opacity: .24;
  }

  .actived {
    color: #ff8309;
  }

  .fixedArea {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
  }

  .searchHistory {
    width: calc(100% - 0.8rem);
    height: 100%;
    padding: 0 0.4rem;
    position: fixed;
    top: 1.2rem;
    left: 0;
    background: #e6e6e6;
    z-index: 999;
  }

  .historyHead {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .historyList {
    list-style: none;
  }

  .historyList li {
    float: left;
    padding: 0 0.2rem;
    font-size: 0.4rem;
  }

  .clearFix {
    clear: both;
  }

  .usertype {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -0.1rem;
    bottom: -0.1rem;
    color: #fffcfc;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    border: 2px solid #fffcfc;
    background: #ffd200;
    font-size: 0.373333rem;
  }

  .star {
    transform: scale(0.8);
    margin-left: -0.4rem;
  }
</style>



